html {
  display: table;
  margin: auto;
}

h1 {
  color:#FD3456;
  text-align: center;
}

body {
  font-family: monospace;
  font-size: 2em;
  background-color: #290042;
  color:#fff;
  display: table-cell;
  vertical-align: middle;
}

#tbl-main {
  margin-left:auto;
  margin-right:auto;
}

.td-enter-hint {
  text-align:left;
  color:#00ff00;
  font-size:0.8em;
}

.inp-unicode {
  font-size: 3.5em;
  width:1.4em;
  background-color: #290042;
  border:none;
  color:#fff;
  padding:0.1em;
  text-align: center;
}

.div-binarybyte {
  position: relative;
  height:1.5em;
}

.inp-binarybyte {
  font-size: 1em;
  font-family: monospace;
  width:5em;
  border:none;
  text-align: left;
  position: absolute;
  top:0;
  left:0;
  right:0;
  margin-top:0.15em;
  margin-left:auto;
  margin-right:auto;
  padding:0 0 0 0.2em;
  z-index:1;
  color: transparent;
  background: transparent;
  caret-color: #fff;
}

.pre-binarybyte {
  font-size: 1em;
  width:5em;
  text-align: left;
  position: absolute;
  top:0;
  left:0;
  right:0;
  margin-top:0.15em;
  margin-left:auto;
  margin-right:auto;
  padding:0 0 0 0.2em;
  z-index:0;
}

.div-hexbyte {
  position: relative;
  height:2.0em;
}

.inp-hexbyte {
  font-size: 1.5em;
  font-family: monospace;
  width:1.6em;
  border:none;
  text-align: left;
  position: absolute;
  top:0;
  left:0;
  right:0;
  margin-top:0.15em;
  margin-left:auto;
  margin-right:auto;
  padding:0 0 0 0.2em;
  z-index:1;
  color: transparent;
  background: transparent;
  caret-color: #fff;
}

.pre-hexbyte {
  font-size: 1.5em;
  width:1.6em;
  text-align: left;
  position: absolute;
  top:0;
  left:0;
  right:0;
  margin-top:0.15em;
  margin-left:auto;
  margin-right:auto;
  padding:0 0 0 0.2em;
  z-index:0;
}

.div-infobyte {
  padding-top:0.5em;
  font-size:0.65em;
}

.div-codepoint {
  font-size:0.8em;
  margin-top:0.5em;
}

.clr-leading {
  color:#ff00ff;
}

.clr-payload-bits {
  color:#ffff00;
}

.clr-missing-bits {
  color:#5F656D;
}

.clr-continuation {
  color:#65D1FF;
}

.clr-single {
  color:#ff00ff;
}

.clr-invalid {
  color:#FD3456;
}
